const statusMapA = {
  // 0状态挂起
  0: '草稿',
  1: '草稿',
  2: '待开始',
  3: '进行中',
  4: '待结算',
  5: '已结束',
};
const statusMapI = {
  0: '待审核',
  1: '审核驳回',
  2: '审核通过',
};
const statusMapT = {
  '-1': '无计划',
  1: '草稿',
  2: '待投放',
  3: '投放中',
  4: '暂停',
  5: '结束',
  6: '关闭',
  7: '广告主暂停投放',
  8: '广告主结束投放',
  71: '用户余额不足',
  72: '达到任务投放总上限',
  73: '达到用户任务日投放上限',
  74: '达到广告主日投放上限',
};
const statusMapIV = {
  '-1': '待发布',
  0: '待处理',
  1: '拒绝邀请',
  2: '接受邀请',
  3: '取消参加',
};
export default function statusFilter(value, type) {
  let output;
  switch (type) {
    case 'a':
      output = statusMapA[value];
      break;
    case 'i':
      output = statusMapI[value];
      break;
    case 't':
      output = statusMapT[value];
      break;
    case 'iv':
      output = statusMapIV[value];
      break;
    default:
      output = statusMapA[value];
      break;
  }
  return output;
}



