const statusMap = {
  '-1': '无广告',
  1: '草稿',
  2: '待投放',
  3: '投放中',
  4: '暂停',
  5: '结束',
  6: '关闭',
  7: '广告主暂停投放',
  8: '广告主结束投放',
  71: '用户余额不足',
  72: '达到任务投放总上限',
  73: '达到用户任务日投放上限',
  74: '达到广告主日投放上限',
};

export default function getAdStatusStr(adStatus, taskStatus, reason) {
  switch (adStatus) {
    case 0:
      return '待审核';
    case 1:
      return statusMap[taskStatus];
    case 2:
      return `审核驳回 (驳回原因: ${reason})`;
    default:
      break;
  }
  return '没有广告';
}



